import { Browser, isBlazor, isNullOrUndefined } from '@syncfusion/ej2-base';
import { isGroupAdaptive } from '../base/util';
import { RowModelGenerator } from '../services/row-model-generator';
import { GroupModelGenerator } from '../services/group-model-generator';
/**
 * Content module is used to render grid content
 */
export class VirtualRowModelGenerator {
    constructor(parent) {
        this.cOffsets = {};
        this.cache = {};
        this.rowCache = {};
        this.data = {};
        this.groups = {};
        this.parent = parent;
        this.model = this.parent.pageSettings;
        this.rowModelGenerator = this.parent.allowGrouping ? new GroupModelGenerator(this.parent) : new RowModelGenerator(this.parent);
    }
    generateRows(data, notifyArgs) {
        let info = notifyArgs.virtualInfo = notifyArgs.virtualInfo || this.getData();
        let xAxis = info.sentinelInfo && info.sentinelInfo.axis === 'X';
        let page = !xAxis && info.loadNext && !info.loadSelf ? info.nextInfo.page : info.page;
        let result = [];
        let center = ~~(this.model.pageSize / 2);
        let indexes = this.getBlockIndexes(page);
        let loadedBlocks = [];
        this.checkAndResetCache(notifyArgs.requestType);
        if (isGroupAdaptive(this.parent) && this.parent.vcRows.length) {
            return result = this.parent.vcRows;
        }
        if (this.parent.enableColumnVirtualization) {
            for (let i = 0; i < info.blockIndexes.length; i++) {
                if (this.isBlockAvailable(info.blockIndexes[i])) {
                    this.cache[info.blockIndexes[i]] = this.rowModelGenerator.refreshRows(this.cache[info.blockIndexes[i]]);
                }
            }
        }
        if (isBlazor() && this.parent.isServerRendered) {
            let virtualStartIdx = 'virtualStartIndex';
            let startIndex = 'startIndex';
            let endIndex = 'endIndex';
            if (!notifyArgs[virtualStartIdx] && Object.keys(this.rowCache).length === 0) {
                for (let i = 0; i < data.length; i++) {
                    let args = [];
                    args.push(data[i]);
                    this.rowCache[i] = this.rowModelGenerator.generateRows(args, { startIndex: i })[0];
                }
                let j = 0;
                for (let i = 0; i < this.parent.pageSettings.pageSize; i++) {
                    result[j] = this.rowCache[i];
                    j++;
                }
            }
            else if (notifyArgs[virtualStartIdx]) {
                let virtualStartIndex = notifyArgs[startIndex];
                let cacheindex = [];
                for (let i = 0; i < Object.keys(this.rowCache).length; i++) {
                    cacheindex.push(Number(Object.keys(this.rowCache)[i]));
                }
                for (let i = 0; i < data.length; i++) {
                    let args = [];
                    let check = cacheindex.indexOf(virtualStartIndex);
                    args.push(data[i]);
                    if (check === -1) {
                        this.rowCache[virtualStartIndex] = this.rowModelGenerator.generateRows(args, { startIndex: virtualStartIndex })[0];
                    }
                    virtualStartIndex++;
                }
            }
            if (!isNullOrUndefined(notifyArgs[virtualStartIdx])) {
                let j = 0;
                for (let i = notifyArgs[startIndex]; i < notifyArgs[endIndex]; i++) {
                    result[j] = this.rowCache[i];
                    j++;
                }
            }
        }
        else {
            let values = info.blockIndexes;
            for (let i = 0; i < values.length; i++) {
                if (!this.isBlockAvailable(values[i])) {
                    let rows = this.rowModelGenerator.generateRows(data, {
                        virtualInfo: info, startIndex: this.getStartIndex(values[i], data)
                    });
                    if (isGroupAdaptive(this.parent) && !this.parent.vcRows.length) {
                        this.parent.vRows = rows;
                        this.parent.vcRows = rows;
                    }
                    let median;
                    if (isGroupAdaptive(this.parent)) {
                        median = this.model.pageSize / 2;
                        if (!this.isBlockAvailable(indexes[0])) {
                            this.cache[indexes[0]] = rows.slice(0, median);
                        }
                        if (!this.isBlockAvailable(indexes[1])) {
                            this.cache[indexes[1]] = rows.slice(median, this.model.pageSize);
                        }
                    }
                    else {
                        median = ~~Math.max(rows.length, this.model.pageSize) / 2;
                        if (!this.isBlockAvailable(indexes[0])) {
                            this.cache[indexes[0]] = rows.slice(0, median);
                        }
                        if (!this.isBlockAvailable(indexes[1])) {
                            this.cache[indexes[1]] = rows.slice(median);
                        }
                    }
                }
                if (this.parent.groupSettings.columns.length && !xAxis && this.cache[values[i]]) {
                    this.cache[values[i]] = this.updateGroupRow(this.cache[values[i]], values[i]);
                }
                result.push(...this.cache[values[i]]);
                if (this.isBlockAvailable(values[i])) {
                    loadedBlocks.push(values[i]);
                }
            }
            info.blockIndexes = loadedBlocks;
        }
        if (!isBlazor() || (isBlazor() && !this.parent.isServerRendered)) {
            let grouping = 'records';
            if (this.parent.allowGrouping) {
                this.parent.currentViewData[grouping] = result.map((m) => m.data);
            }
            else {
                this.parent.currentViewData = result.map((m) => m.data);
            }
        }
        return result;
    }
    getBlockIndexes(page) {
        return [page + (page - 1), page * 2];
    }
    getPage(block) {
        return block % 2 === 0 ? block / 2 : (block + 1) / 2;
    }
    isBlockAvailable(value) {
        return value in this.cache;
    }
    getData() {
        return {
            page: this.model.currentPage,
            blockIndexes: this.getBlockIndexes(this.model.currentPage),
            direction: 'down',
            columnIndexes: this.parent.getColumnIndexesInView()
        };
    }
    getStartIndex(blk, data, full = true) {
        let page = this.getPage(blk);
        let even = blk % 2 === 0;
        let index = (page - 1) * this.model.pageSize;
        return full || !even ? index : index + ~~(this.model.pageSize / 2);
    }
    getColumnIndexes(content = this.parent.getHeaderContent().querySelector('.e-headercontent')) {
        if (this.parent.getFrozenColumns()) {
            content = content.querySelector('.e-movableheader');
        }
        let indexes = [];
        let sLeft = content.scrollLeft | 0;
        let keys = Object.keys(this.cOffsets);
        let cWidth = content.getBoundingClientRect().width;
        sLeft = Math.min(this.cOffsets[keys.length - 1] - cWidth, sLeft);
        let calWidth = Browser.isDevice ? 2 * cWidth : cWidth / 2;
        let left = sLeft + cWidth + (sLeft === 0 ? calWidth : 0);
        keys.some((offset, indx, input) => {
            let iOffset = Number(offset);
            let offsetVal = this.cOffsets[offset];
            let border = sLeft - calWidth <= offsetVal && left + calWidth >= offsetVal;
            if (border) {
                indexes.push(iOffset);
            }
            return left + calWidth < offsetVal;
        });
        if (isBlazor() && this.parent.isServerRendered) {
            this.parent.contentModule.startColIndex = indexes[0];
            this.parent.contentModule.endColIndex = indexes[indexes.length - 1];
        }
        this.addFrozenIndex(indexes);
        return indexes;
    }
    addFrozenIndex(indexes) {
        if (this.parent.getFrozenColumns() && this.parent.enableColumnVirtualization && indexes[0] === 0) {
            for (let i = 0; i < this.parent.getFrozenColumns(); i++) {
                indexes.push(indexes[indexes.length - 1] + 1);
            }
        }
    }
    checkAndResetCache(action) {
        let actions = ['paging', 'refresh', 'sorting', 'filtering', 'searching', 'grouping', 'ungrouping', 'reorder',
            'save', 'delete'];
        if (this.parent.getFrozenColumns() && this.parent.frozenRows && this.parent.enableColumnVirtualization && action === 'reorder') {
            actions.splice(actions.indexOf(action), 1);
        }
        let clear = actions.some((value) => action === value);
        if (clear) {
            this.cache = {};
            this.data = {};
            this.groups = {};
        }
        return clear;
    }
    refreshColOffsets() {
        let col = 0;
        this.cOffsets = {};
        let gLen = this.parent.groupSettings.columns.length;
        let cols = this.parent.columns;
        let cLen = cols.length;
        let isVisible = (column) => column.visible &&
            (!this.parent.groupSettings.showGroupedColumn ? this.parent.groupSettings.columns.indexOf(column.field) < 0 : column.visible);
        let c = this.parent.groupSettings.columns;
        for (let i = 0; i < c.length; i++) {
            this.cOffsets[i] = (this.cOffsets[i - 1] | 0) + 30;
        }
        let blocks = Array.apply(null, Array(cLen)).map(() => col++);
        for (let j = 0; j < blocks.length; j++) {
            blocks[j] = blocks[j] + gLen;
            this.cOffsets[blocks[j]] = (this.cOffsets[blocks[j] - 1] | 0) + (isVisible(cols[j]) ? parseInt(cols[j].width, 10) : 0);
        }
    }
    updateGroupRow(current, block) {
        let currentFirst = current[0];
        let rows = [];
        let keys = Object.keys(this.cache);
        for (let i = 0; i < keys.length; i++) {
            if (Number(keys[i]) < block) {
                rows = [...rows, ...this.cache[keys[i]]];
            }
        }
        if ((currentFirst && currentFirst.isDataRow) || block % 2 === 0) {
            return current;
        }
        return this.iterateGroup(current, rows);
    }
    iterateGroup(current, rows) {
        let currentFirst = current[0];
        let offset = 0;
        if (currentFirst && currentFirst.isDataRow) {
            return current;
        }
        let isPresent = current.some((row) => {
            return rows.some((oRow, index) => {
                let res = oRow && oRow.data.field !== undefined && oRow.data.field === row.data.field &&
                    oRow.data.key === row.data.key;
                if (res) {
                    offset = index;
                }
                return res;
            });
        });
        if (isPresent) {
            current.shift();
            current = this.iterateGroup(current, rows.slice(offset));
        }
        return current;
    }
    getRows() {
        let rows = [];
        let isBlazorServerRendered = isBlazor() && this.parent.isServerRendered ? true : false;
        let keys = isBlazorServerRendered ? Object.keys(this.rowCache) : Object.keys(this.cache);
        for (let i = 0; i < keys.length; i++) {
            rows = isBlazorServerRendered ? [...rows, ...this.rowCache[keys[i]]] : [...rows, ...this.cache[keys[i]]];
        }
        return rows;
    }
}
